//   SimpleLightbox   :
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';
const gallerySL = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 300,
  // disableScroll: false,
});

let page; //  .      !!!
let request = ''; //      ,    
let remainsItems = 0; //   

const THROTTLE_DELAY = 1000;

function getPage() {
  return page;
}
function setPage(arg) {
  page = arg;
}

function getRequest() {
  return request;
}
function setRequest(arg) {
  request = arg;
}

function getRemainsItems() {
  return remainsItems;
}
function setRemainsItems(arg) {
  remainsItems = arg;
}

export {
  getPage,
  setPage,
  getRequest,
  setRequest,
  getRemainsItems,
  setRemainsItems,
  THROTTLE_DELAY,
  gallerySL,
};
